import React, { useLayoutEffect, useEffect } from 'react';
import { Row, Col } from 'antd';
import classNames from 'classnames';
import { formatEther } from 'ethers/lib/utils';
import TitleCombo from '../../components/UI/TitleCombo/TitleCombo';
import MainContainer from '../../components/UI/MainContainer/MainContainer';
import CustomButton from '../../components/UI/CustomButton/CustomButton';
import CurrentBenifitBackground from './CurrentBenifitBackground.png';
import First from './First.png';
import Second from './Second.png';
import Third from './Third.png';
import FirstBg from './FirstBackground.png';
import SecondBg from './SecondBackground.png';
import ThirdBg from './ThirdBackground.png';
import { useZPointStatistic } from '../../hooks/useManager';
import { formatNumber, truncAddress } from '../../utils/utils';
import { ZPOINTSTATISTIC_POINT_KEYS } from '../../constants/misc';
import { usePoints } from '../../hooks/usePoints';
import { useWallet } from '../../store/wallet-context';
import NavLinkWithQuery from '../../components/NavLinkWithQuery/NavLinkWithQuery';

import styles from './Points.module.scss';

function CurrentBenifit() {
  const content = (
    <Col span={24} xl={14}>
      <h2 className={styles.secondaryTitle}>Zebec Points Reward</h2>
      <Row gutter={32} align="middle">
        <Col span={24} md={12}>
          To better incentivize early validators, Zepoch node holders will be airdropped with both
          ZBC and USDT tokens based on the weighted average Zebec Points of the builders.
        </Col>
        <Col span={24} md={12}>
          <table>
            <tbody>
              <tr>
                <td>Zepoch Sold</td>
                <td>Airdrop Amount</td>
              </tr>
              <tr>
                <td>3000</td>
                <td>1,000,000 USDT</td>
              </tr>
              <tr>
                <td>8000</td>
                <td>4,000,000 USDT</td>
              </tr>
            </tbody>
          </table>
        </Col>
      </Row>
    </Col>
  );
  return (
    <Row className={styles.currentBenifitBackground}>
      <Col span={0} xl={10}>
        <img src={CurrentBenifitBackground} alt="" />
      </Col>
      <Col span={0} xl={24} className={styles.currentBenifitContainer}>
        <Row justify="end">{content}</Row>
      </Col>
      <Col span={24} xl={0} className={styles.currentBenifitContainerSmall}>
        <Row justify="center">
          <Col
            span={20}
            md={12}
            lg={8}
            xl={0}
            className={styles.currentBenifitBackgroundImageSmall}>
            <img src={CurrentBenifitBackground} alt="" />
          </Col>
          {content}
        </Row>
      </Col>
    </Row>
  );
}

export default function Points() {
  const { data: zPointStatistic } = useZPointStatistic();
  const { account } = useWallet();
  useLayoutEffect(() => {
    window.scrollTo(0, 0);
  }, []);
  const { rankList, myRanking, getPointsRanking, getMyRanking } = usePoints();
  useEffect(() => {
    getPointsRanking();
    getMyRanking();
  }, [getPointsRanking, getMyRanking]);

  function formatPoints(str) {
    return str ? formatEther(str) : '-';
  }
  return (
    <div className={styles.Points}>
      <Row className={styles.banner} justify="center">
        <MainContainer className={styles.bannerTextContainer}>
          <Row className={styles.bannerText}>
            <Col>
              <TitleCombo>
                <h2>Zebec Points System</h2>
                <p>
                  Zebec points are a voucher system designed to reward those who buy Zepoch Nodes or
                  help friends purchase them. The more points you have, the greater your rewards
                  from the Zebec airdrops. They measure contributions to the community in order to
                  incentivize growth.
                </p>
              </TitleCombo>
            </Col>
          </Row>
        </MainContainer>
        <Col className={styles.bannerContainer}>
          <Row className={styles.rankContainer}>
            <Col span={24}>
              <Row className={styles.mobileJustifyCenter}>
                <h2 className={styles.secondaryTitle}>Zebec Points Leaderboard</h2>
              </Row>
            </Col>
            <Col span={24} md={9}>
              <div className={styles.rankListTopThree}>
                <div className={classNames(styles.rankRow, styles.first)}>
                  <div className={styles.rankItem}>
                    <img className={styles.rankOrdinal} src={First} alt="first" />
                    <img className={styles.rankItemBg} src={FirstBg} alt="bg" />
                    <div className={styles.rankItemText}>
                      <p
                        className={classNames({
                          [styles.myAddress]: rankList?.[0]?.isCurrentUser,
                        })}>
                        {truncAddress(rankList?.[0]?.address)}{' '}
                        {rankList?.[0]?.isCurrentUser && '(you)'}
                      </p>
                      <p>{formatPoints(rankList?.[0]?.points)}</p>
                    </div>
                  </div>
                </div>
                <div className={classNames(styles.rankRow, styles.second)}>
                  <div className={styles.rankItem}>
                    <img className={styles.rankOrdinal} src={Second} alt="first" />
                    <img className={styles.rankItemBg} src={SecondBg} alt="bg" />
                    <div className={styles.rankItemText}>
                      <p
                        className={classNames({
                          [styles.myAddress]: rankList?.[1]?.isCurrentUser,
                        })}>
                        {truncAddress(rankList?.[1]?.address)}{' '}
                        {rankList?.[1]?.isCurrentUser && '(you)'}
                      </p>
                      <p>{formatPoints(rankList?.[1]?.points)}</p>
                    </div>
                  </div>
                  <div className={styles.rankItem}>
                    <img className={styles.rankOrdinal} src={Third} alt="first" />
                    <img className={styles.rankItemBg} src={ThirdBg} alt="bg" />
                    <div className={styles.rankItemText}>
                      <p
                        className={classNames({
                          [styles.myAddress]: rankList?.[2]?.isCurrentUser,
                        })}>
                        {truncAddress(rankList?.[2]?.address)}{' '}
                        {rankList?.[2]?.isCurrentUser && '(you)'}
                      </p>
                      <p>{formatPoints(rankList?.[2]?.points)}</p>
                    </div>
                  </div>
                </div>
              </div>
            </Col>
            <Col span={24} md={15}>
              <div className={styles.rankList}>
                <Row>
                  <Col span={4} className={styles.rankListHeader}>
                    Rank
                  </Col>
                  <Col span={15} className={styles.rankListHeader}>
                    Address
                  </Col>
                  <Col span={5} className={styles.rankListHeader}>
                    Points
                  </Col>
                </Row>
                {rankList.slice(3).map(({ address, points, isCurrentUser }, index) => (
                  <Row key={address}>
                    <Col
                      span={4}
                      className={classNames(styles.rankListIndex, {
                        [styles.myAddress]: isCurrentUser,
                      })}>
                      {index + 4}
                    </Col>
                    <Col
                      span={15}
                      className={classNames(styles.rankListAddress, {
                        [styles.myAddress]: isCurrentUser,
                      })}>
                      {truncAddress(address)} {isCurrentUser && '(you)'}
                    </Col>
                    <Col
                      span={5}
                      className={classNames(styles.rankListPoints, {
                        [styles.myAddress]: isCurrentUser,
                      })}>
                      {formatPoints(points)}
                    </Col>
                  </Row>
                ))}
                {account && myRanking?.ranking > 10 ? (
                  <Row className={styles.rankListMe}>
                    <Col span={4} className={styles.rankListIndex}>
                      {formatNumber(myRanking.ranking)}
                    </Col>
                    <Col span={15} className={styles.rankListAddress}>
                      <span className={styles.rankListMyAddress}>
                        {truncAddress(myRanking.address)} (you)
                      </span>
                    </Col>
                    <Col span={5} className={styles.rankListPoints}>
                      {formatPoints(myRanking.points)}
                    </Col>
                  </Row>
                ) : (
                  <hr style={{ border: 'none' }} />
                )}
              </div>
              <div className={styles.rankListTip}>
                The points leaderboard updates every 15 minutes.
              </div>
            </Col>
          </Row>
        </Col>
      </Row>
      <div className={styles.contentContainer}>
        <Row className={styles.mobileJustifyCenter}>
          <h2 className={styles.secondaryTitle}>My Points Stats</h2>
        </Row>
        <Row
          className={classNames(styles.totalEarnedPoints, styles.mobileJustifyCenter)}
          align="middle">
          <Col>
            {formatNumber(
              !!Object.keys(zPointStatistic).length &&
                ZPOINTSTATISTIC_POINT_KEYS.reduce((accum, key) => accum + zPointStatistic[key], 0),
            )}
          </Col>
          <Col>&nbsp;Zebec points</Col>
        </Row>
        <Row gutter={16}>
          <Col span={24} md={8} className={styles.earnedSpace}>
            <div className={styles.earnedContainer}>
              <h3>Earned by Purchasing</h3>
              <p>{formatNumber(zPointStatistic.zPointPurchasing)}</p>
              <NavLinkWithQuery to="/participate">
                <CustomButton className={styles.pointsButton}>Buy Nodes</CustomButton>
              </NavLinkWithQuery>
            </div>
          </Col>
          <Col span={24} md={8} className={styles.earnedSpace}>
            <div className={styles.earnedContainer}>
              <h3>Earned by Referral</h3>
              <p>{formatNumber(zPointStatistic.zPointReferral)}</p>
              <NavLinkWithQuery to="/profile">
                <CustomButton className={styles.pointsButton}>Invite Friends</CustomButton>
              </NavLinkWithQuery>
            </div>
          </Col>
          <Col span={24} md={8} className={styles.earnedSpace}>
            <div className={styles.earnedContainer}>
              <h3>Earned by Bonus</h3>
              <p>{formatNumber(zPointStatistic.zPointBonus)}</p>
              <p className={styles.earnedDesc}>
                Top of the leaderboard will be rewarded with bonus points.
              </p>
            </div>
          </Col>
        </Row>
        <CurrentBenifit />
      </div>
    </div>
  );
}
