import TitleCombo from '../../components/UI/TitleCombo/TitleCombo';
import CustomButton from '../../components/UI/CustomButton/CustomButton';
import NumberInput from '../../components/UI/NumberInput/NumberInput';
import { checkTime, formatNumber } from '../../utils/utils';
import {
  useAllTokenAllowance,
  useAllTokenBalance,
  useTokenApprove,
  useZepochNodeTotalSupply,
} from '../../hooks/useToken';
import { useNameservice } from '../../store/nameservice-context';
import { CONTRACT_NAME, SUPPORT_PURCHASE_TOKEN_KEY } from '../../constants/addresses';
import { useWallet } from '../../store/wallet-context';
import {
  useBuy,
  useCalcPhase2Cost,
  useLockZBC,
  usePhase1Config,
  usePhase2Config,
} from '../../hooks/useManager';
import {
  PHASE_1_NFT_AMOUNT,
  SINGLE_TRANSACTION_NFT_LIMIT,
  TOTAL_NFT_AMOUNT,
  WHITELIST_END_TIMESTAMP,
  WHITELIST_START_TIMESTAMP,
} from '../../constants/misc';
import {
  useIsRegistered,
  useRegisteredInvitationCodeContains,
} from '../../hooks/useInvitationCenter';
import { useQuery } from '../../hooks/useQuery';
import { useCurrentPhase } from '../../hooks/useCurrentPhase';
import CustomInput from '../../components/UI/CustomInput/CustomInput';
import NavLinkWithQuery from '../../components/NavLinkWithQuery/NavLinkWithQuery';

import styles from './Participate.module.scss';

const { Countdown } = Statistic;

const { Option } = Select;

const STAGE1NOTE = 'Note: Staked ZBC can be withdrawn after 365 days';

const stage1Reward = (
  <>
    <TitleCombo small>
      <h2>Stage 1 Bonus: Themis Gavel</h2>
      <p className={styles.ParticipateDesc}>
        A Themis Gavel NFT will be given to all Stage 1 Zepoch Node holders for free.
      </p>
    </TitleCombo>
    <div className={styles.ThemisGavel}>
      {/* <img src="/static/images/Participate/themisGavel.png" alt="stage1" data-aos="fade-right" /> */}
      <video
        autoPlay
        muted
        loop
        playsInline
        poster="/static/images/NFT/themis.png"
        data-aos="fade-right">
        <source src="/static/images/NFT/themis.mp4" type="video/mp4" />
      </video>
      <div className={styles.ThemisGavelInfoWarp} data-aos="fade-left">
        <TitleCombo className={styles.ThemisGavelTitle}>
          <h3>Themis Gavel</h3>
          <p className={styles.ParticipateDesc}>
            Themis Gavel is a soul-bound NFT using the (EIP-5114) protocol whose primary benefit is
            giving voting rights in Zebecs governance process to the owner.
          </p>
          <p
            className={styles.ParticipateBuyFormDesc}
            style={{
              color: 'rgba(255, 255, 255, 0.5)',
              marginTop: 0,
            }}>
            Note: Themis Gavel will automatically bond to the address once it is accepted and cannot
            be unbonded, transferred, or traded.
          </p>
        </TitleCombo>

        <div className={classNames(styles.Attention2, styles.BlueText, styles.BlueBg)}>
          <div className={styles.AttentionTitle}>How to get a Themis Gavel NFT:</div>
          <div className={styles.AttentionDesc}>
            <div>
              1. Participate in the 1st stage of Operation Horizon  the first 288 Zepoch Node
              holders will automatically be given a Themis Gavel for free.
            </div>
            <div>
              2. Participate in the 2nd stage of Operation Horizon  complete specific community
              promotion tasks and receive one when the contributions are verified.
            </div>
          </div>
        </div>
      </div>
    </div>
  </>
);

const loadingPlaceholder = (
  <div className={styles.loadingPlaceholder}>
    <Spin />
  </div>
);

function Stake() {
  const queryInviterCode = useQuery()?.get('referral');
  // inputs
  const [amount, setAmount] = useState(1);
  const [agreementChecked, setAgreementChecked] = useState(false);
  const [inviterCode, setInviterCode] = useState(queryInviterCode);
  const [selectedCurrency, setSelectedCurrency] = useState(CONTRACT_NAME.USDT);

  const { account } = useWallet();
  const addresses = useNameservice();
  const { data: isRegistered } = useIsRegistered();
  // get zbc balance and allowance
  const allTokenBalance = useAllTokenBalance();
  const allTokenAllowance = useAllTokenAllowance(addresses[CONTRACT_NAME.Manager]);
  // get total zepoch supply
  const { data: zepochNodeTotalSupply } = useZepochNodeTotalSupply();
  const { data: phase2Cost } = useCalcPhase2Cost(amount);
  const [config, setConfig] = useState();

  // validate invitation
  const { isValidInvitation } = useRegisteredInvitationCodeContains();
  // approve zbc hook
  const approveZBC = useTokenApprove(addresses[CONTRACT_NAME.ZBC]);
  // approve USDT hook
  const approveUSDT = useTokenApprove(addresses[CONTRACT_NAME.USDT]);
  // approve zbc hook
  const approveBUSD = useTokenApprove(addresses[CONTRACT_NAME.BUSD]);

  const approveToken = {
    [CONTRACT_NAME.ZBC]: approveZBC,
    [CONTRACT_NAME.USDT]: approveUSDT,
    [CONTRACT_NAME.BUSD]: approveBUSD,
  };
  // lock zbc hook
  const lockZBC = useLockZBC();
  // buy nft
  const buyNFT = useBuy();

  // get phase config
  const { data: phase1Config } = usePhase1Config();
  const { phase1StartTimestamp, phase1ZbcLockAmount } = phase1Config;
  const { data: phase2Config } = usePhase2Config();
  const { phase2StartTimestamp } = phase2Config;
  const currentPhase = useCurrentPhase();
  // const currentPhase = 1;

  useEffect(() => {
    if (currentPhase) {
      const tempConfig = {};
      switch (currentPhase) {
        case 1:
          tempConfig.currency = CONTRACT_NAME.ZBC;
          tempConfig.startTimestamp = phase1StartTimestamp;
          tempConfig.totalPrice = phase1ZbcLockAmount * amount;
          tempConfig.unitPrice = phase1ZbcLockAmount;
          tempConfig.totalNFT = PHASE_1_NFT_AMOUNT;
          tempConfig.tagText = `Stake $ZBC to get a Zepoch Node`;
          tempConfig.note = STAGE1NOTE;
          break;
        case 2:
          tempConfig.currency = selectedCurrency;
          tempConfig.startTimestamp = phase2StartTimestamp;
          tempConfig.totalPrice = phase2Cost;
          tempConfig.unitPrice = tempConfig.totalPrice / amount;
          tempConfig.totalNFT = TOTAL_NFT_AMOUNT;
          tempConfig.tagText = `Buy Zepoch Node with USDT/BUSD`;
          break;
        default:
      }
      tempConfig.phase = currentPhase;
      setConfig(tempConfig);
    }
  }, [
    currentPhase,
    amount,
    phase1StartTimestamp,
    phase1ZbcLockAmount,
    phase2StartTimestamp,
    selectedCurrency,
    phase2Cost,
  ]);

  if (!config) {
    return loadingPlaceholder;
  }

  const { data: allowance } = allTokenAllowance[config.currency];
  const { data: balance } = allTokenBalance[config.currency];
  const { state: approveTokenState, approve } = approveToken[config.currency];
  const { state: getNFTState, [config.phase === 1 ? 'lockZBC' : 'buy']: getNFT } =
    config.phase === 1 ? lockZBC : buyNFT;

  const handleApprove = () => {
    if (approveTokenState.isLoading || !addresses[CONTRACT_NAME.Manager]) {
      return;
    }
    if (!account) {
      message.info('Please connect to wallet');
      return;
    }
    if (!checkTime(config.startTimestamp * 1000)) {
      message.info('Not start yet');
      return;
    }
    if (!agreementChecked) {
      message.info('Please check the box for the risk agreement');
      return;
    }
    approve(addresses[CONTRACT_NAME.Manager]);
  };

  const handleGetNFT = async () => {
    if (getNFTState.isLoading || !addresses[config.currency]) {
      return;
    }
    if (!account) {
      message.info('Please connect to wallet');
      return;
    }
    if (!checkTime(config.startTimestamp * 1000)) {
      message.info('Not start yet');
      return;
    }
    // test for agreement checked
    if (!agreementChecked) {
      message.info('Please check the box for the risk agreement');
      return;
    }
    if (amount <= 0) {
      message.info('Please enter a valid amout');
      return;
    }
    // test enough purchase balance
    if (config.totalPrice > balance) {
      message.info(`Not enough ${config.currency}`);
      return;
    }
    // test enough nft to sell in current phase
    if (amount + zepochNodeTotalSupply > config.totalNFT) {
      message.info('Not enough Zepoch Node left');
      return;
    }
    // prevent out of gas
    if (amount > SINGLE_TRANSACTION_NFT_LIMIT) {
      message.info(
        `A maximum of ${SINGLE_TRANSACTION_NFT_LIMIT} Nodes can be acquired in a single transaction`,
      );
      return;
    }

    let invitationCode = inviterCode;
    // handle invitation code
    if (!(isRegistered || (await isValidInvitation(invitationCode)))) {
      message.info('Invalid invitation code');
      return;
    }
    if (isRegistered) {
      invitationCode = '';
    }
    if (config.phase === 1) {
      getNFT(amount, invitationCode);
      return;
    }
    getNFT(addresses[config.currency], amount, invitationCode);
  };

  const handleAgreementCheckedChange = (e) => {
    setAgreementChecked(e.target.checked);
  };

  const handleSelectedCurrencyChange = (value) => {
    setSelectedCurrency(value);
  };

  const handleInviterCodeChange = (e) => {
    setInviterCode(e.target.value);
  };

  const getButton = () => {
    if (zepochNodeTotalSupply === config.totalNFT) {
      return (
        <CustomButton className={styles.BuyButton} disabled rounded>
          <div className={styles.buyButtonContent}>Sale Ended</div>
        </CustomButton>
      );
    }
    if (!allowance) {
      return (
        <CustomButton
          className={styles.BuyButton}
          rounded
          loading={approveTokenState.isLoading}
          onClick={handleApprove}>
          <div className={styles.buyButtonContent}>
            APPROVE {config.currency} <br />
            <span>
              Balance: {formatNumber(balance)} {config.currency}
            </span>
          </div>
        </CustomButton>
      );
    }
    return (
      <CustomButton
        className={styles.BuyButton}
        rounded
        loading={getNFTState.isLoading}
        onClick={handleGetNFT}>
        <div className={styles.buyButtonContent}>
          Get Zepoch
          <br />
          <span>
            Balance: {formatNumber(balance)} {config.currency}
          </span>
        </div>
      </CustomButton>
    );
  };

  const stakeContent = config && (
    <>
      <div className={styles.ProgressBarWarp}>
        <div className={classNames(styles.Stage, styles.Begins)}>
          {config.startTimestamp && !checkTime(config.startTimestamp * 1000) ? (
            <div className={styles.StageCountdown}>
              Begins in <Countdown value={config.startTimestamp * 1000} format="D:HH:mm:ss" />
            </div>
          ) : (
            <div />
          )}
          {config.totalNFT && (
            <div className={styles.StageServings}>
              <span className={styles.StageServingsActive}>
                {formatNumber(zepochNodeTotalSupply)}
              </span>{' '}
              / {formatNumber(config.totalNFT)}
            </div>
          )}
        </div>
        {config.totalNFT && (
          <div className={styles.ProgressBar}>
            <div
              className={styles.ProgressBarActive}
              style={{ width: `${(zepochNodeTotalSupply * 100) / config.totalNFT}%` }}
            />
          </div>
        )}
      </div>
      <div className={classNames(styles.ParticipateCard, styles.ParticipateBuy)}>
        <div className={styles.Stage}>
          <div className={styles.StageTag}>
            Stage {config.phase} <span>{config.tagText}</span>
          </div>
        </div>

        <div className={styles.ParticipateBuyWarp}>
          <div className={styles.ParticipateBuyForm}>
            <TitleCombo className={styles.ParticipateBuyFormTitle}>
              <h3>Zepoch Node NFTs</h3>
              <p className={styles.ParticipateBuyFormDesc}>
                Zepoch Node NFTs guarantee your right to run a validator node on Zebec chain when it
                goes live in 2023. Zepoch Node NFTs will prove your identity so you can run a node
                in the future; holding it now will give you USDT and ZBC rewards.
              </p>
              {config.note && (
                <p
                  className={styles.ParticipateBuyFormDesc}
                  style={{
                    color: 'rgba(255, 255, 255, 0.5)',
                  }}>
                  {config.note}
                </p>
              )}
            </TitleCombo>
            <div className={styles.Price}>
              <div>
                <div className={styles.StageServings}>
                  <span className={styles.StageServingsActive}>
                    {formatNumber(zepochNodeTotalSupply)}
                  </span>{' '}
                  / {formatNumber(config.totalNFT)} nodes sold
                </div>
              </div>
              {config.phase === 1 && (
                <div>
                  Price: {formatNumber(config.unitPrice)} {config.currency}
                </div>
              )}
              {config.phase === 2 && (
                <Select
                  value={selectedCurrency}
                  suffixIcon={<CaretDownOutlined />}
                  onChange={handleSelectedCurrencyChange}
                  popupClassName="currencyDropdown"
                  bordered={false}>
                  {SUPPORT_PURCHASE_TOKEN_KEY.map((item) => (
                    <Option key={item} className={styles.currencyOption}>
                      <img src={`/static/images/TokenIcon/${item.toLowerCase()}.svg`} alt={item} />
                      {item}
                    </Option>
                  ))}
                </Select>
              )}
            </div>
            <NumberInput value={amount} onChange={(v) => setAmount(v)} />
            {!isRegistered && (
              <CustomInput
                placeholder="Enter Your Invitation Code"
                value={inviterCode}
                onChange={handleInviterCodeChange}
              />
            )}
            <div className={styles.Cost}>
              <span>
                Cost: {formatNumber(config.totalPrice)} {config.currency}
              </span>
            </div>

            <div className={styles.Protocol}>
              <Checkbox onChange={handleAgreementCheckedChange}>
                I understand that by participating, I assume all risks associated with the campaign.
              </Checkbox>
            </div>
            {getButton()}
          </div>
          <div className={styles.ParticipateBuyImageWarp}>
            <video
              autoPlay
              muted
              loop
              playsInline
              poster="/static/images/NFT/zepoch.png"
              data-aos="fade-left">
              <source src="/static/images/NFT/zepoch.mp4" type="video/mp4" />
            </video>
          </div>
        </div>

        <div className={styles.Attention}>
          <div className={styles.AttentionTitle}>
            Attention
            <img src="/static/images/Participate/questionIcon.svg" alt="Attention" />
          </div>
          <div className={styles.AttentionDesc}>
            The Zepoch Node NFT cannot be transferred or traded until the network construction is
            completed. (before all 30,000 are sold.)
          </div>
        </div>
      </div>
    </>
  );

  return (
    <>
      <TitleCombo>
        <h2>Get a Zepoch NFT and Start Earning</h2>
        <p className={styles.ParticipateDesc}>
          When you hold a Zepoch Node NFT, you help grow the Zebec ecosystem and get the privilege
          to run a validator node when the chain launches. #Hodl a Zepoch Node NFT and receive USDT
          and ZBC rewards!
          <br />
          <NavLinkWithQuery to="/LearnMore">Learn More.</NavLinkWithQuery>
        </p>
      </TitleCombo>
      {stakeContent}
      {config.phase === 1 && stage1Reward}
      <div className={styles.Invite}>
        <div className={styles.InviteContentWarp}>
          <div className={styles.InviteLeft}>
            <img
              className={styles.InviteLogo}
              src="/static/images/Participate/logo.svg"
              alt="zebec"
            />
            <div className={styles.InviteTitle}>
              <div className={styles.InviteTitleText}>Points Giveaway</div>
              {config.phase === 2 && (
                <div className={styles.Rrewards}>
                  <img
                    className={styles.InviteLogo}
                    src="/static/images/Participate/pointIcon.svg"
                    alt="zebec"
                  />
                  5% Referral Rrewards
                </div>
              )}
              <NavLinkWithQuery to="/profile">
                <CustomButton className={styles.buyButton} rounded>
                  <span className={styles.buyButtonText}>Invite now</span>
                </CustomButton>
              </NavLinkWithQuery>
            </div>
            <div className={styles.InviteDesc}>
              Each successful referred purchase of Zeopch Node will be rewarded 1000 points
            </div>
          </div>
          {/* <div className={styles.InviteRight}>
            <Link to="/profile">
              <CustomButton className={styles.buyButton} rounded>
                <span className={styles.buyButtonText}>Invite now</span>
              </CustomButton>
            </Link>
          </div> */}
        </div>
        <img
          className={styles.InviteTextBg}
          src="/static/images/About/saleWatermark.svg"
          alt="nodeonSale"
        />
        <img
          className={styles.InviteAwesome}
          src="/static/images/Participate/awesome.png"
          alt="zebec"
        />
      </div>
    </>
  );
}

function Coming() {
  const whitelistStarted = checkTime(WHITELIST_START_TIMESTAMP * 1000);

  return (
    <div className={styles.Coming}>
      <TitleCombo className={styles.ComingWarp}>
        <h2>Zebec Operation Horizon Whitelist</h2>
        <a
          className={styles.learnMoreLink}
          href="https://medium.com/zebec-protocol/announcing-operation-horizon-co-build-an-ecosystem-with-zebec-c58b89431fb"
          target="_blank"
          rel="noopener noreferrer">
          Learn More
        </a>
        {whitelistStarted ? (
          <>
            <div className={styles.CountDown}>
              <span>Ends in:</span>
              <Countdown value={WHITELIST_END_TIMESTAMP * 1000} format="D:HH:mm:ss" />
            </div>
            <a
              href="https://notionforms.io/forms/whitelist-1"
              target="_blank"
              rel="noopener noreferrer">
              <CustomButton rounded>Join the Whitelist</CustomButton>
            </a>
          </>
        ) : (
          <>
            <div className={styles.CountDown}>
              <span>Opens in:</span>
              <Countdown value={WHITELIST_START_TIMESTAMP * 1000} format="D:HH:mm:ss" />
            </div>
            <CustomButton
              rounded
              disabled
              onClick={() => {
                message.info('Open Soon');
              }}>
              Join the Whitelist
            </CustomButton>
          </>
        )}
      </TitleCombo>
      <img src="/static/images/Participate/countdownBg.png" alt="nodeonSale" />
    </div>
  );
}

const Participate = () => {
  const whiteListEnded = checkTime(WHITELIST_END_TIMESTAMP * 1000);
  useLayoutEffect(() => {
    window.scrollTo(0, 0);
  }, []);
  return (
    <div className={styles.Participate} data-aos="fade-up">
      {/* {whiteListEnded ? <Stake /> : <Coming />} */}
      <Stake />
    </div>
  );
};

export default Participate;
